import React from 'react';
import XLSX from 'xlsx'; // Dependency will be dropped
import styleSheet from'./cellRenderer.scss';
import ResizeHandle from './ResizeHandle/ResizeHandle';

function cellRenderer({columnIndex, key, style, parent}) {
    return (
        <div
            key={key}
            style={style}
            
        >
            
            <Cell
                onClick={parent.props.onClick}
                columnIndex={columnIndex}
                onResize={parent.props.onCellResize}
                selection={parent.props.selection}
            ></Cell>
        </div>
    )
}

class Cell extends React.PureComponent {
    constructor(props) {
        super(props);
    }

    handleClick = () => {
        this.props.onClick(this.props.columnIndex);
    }

    handleResize = (offset) => {
        const payload = {
            offset: offset,
            column: this.props.columnIndex,
        }
        this.props.onResize(payload);
    }

    render() {
        let classes = styleSheet.cell;
        if (
            this.props.selection &&
            this.props.columnIndex >= this.props.selection.from &&
            this.props.columnIndex <= this.props.selection.to
        ) {
            classes += ' ' + styleSheet.selected;
        }

        return (
            <div
                className={classes}
                onClick={this.handleClick}
            >
                {XLSX.utils.encode_col(this.props.columnIndex)}
                <ResizeHandle
                    onResize={this.handleResize}
                ></ResizeHandle>
            </div>
        )
    }
}

export default cellRenderer;