import React from 'react';
import styleSheet from './cellRenderer.scss';

function cellRenderer({ columnIndex, rowIndex, key, style, parent }) {
    function handleMouseDown(e) {
        parent.props.onCellMouseDown({ columnIndex, rowIndex });
    }

    function handleChange() {
        
    }

    let classes = styleSheet.Cell;
    if (parent.props.selectedArea) {
        classes += ' ' + calculateCellClasses(
            {column: columnIndex, row: rowIndex},
            parent.props.selection,
            parent.props.selectedArea
        );
    }

    return (
        <div
            className={classes}
            key={key}
            style={style}
            onMouseDown={handleMouseDown}
            onMouseOver={() => (parent.props.onCellMouseOver({ columnIndex, rowIndex }))}
        >
            <input onChange={handleChange} />
        </div>
    )
}

/**
 * Helpter function. Determines the style/classes to apply to the cell.
 */
function calculateCellClasses(position, selection, selectedArea) {
    let classes = '';

    // Check if the cell is inside the selected area
    if (position.column < selectedArea.x1 ||
        position.column > selectedArea.x0 ||
        position.row < selectedArea.y1 ||
        position.row > selectedArea.y0) {
        // Not inside
        return classes;
    }

    if (!(selection.startSelection.row === position.row &&
        selection.startSelection.column === position.column)) {
        classes = classes + ' ' + styleSheet.marked;
    }

    // Aligned to top?
    if (position.row === selectedArea.y1) {
        classes = classes + ' ' + styleSheet.borderTop;
    }

    // Aligned to bottom?
    if (position.row === selectedArea.y0) {
        classes = classes + ' ' + styleSheet.borderBottom;
    }

    // Aligned to left hand side?
    if (position.column === selectedArea.x1) {
        classes = classes + ' ' + styleSheet.borderLeft;
    }

    // Aligned to right hand side?
    if (position.column === selectedArea.x0) {
        classes = classes + ' ' + styleSheet.borderRight;
    }

    // Resize marker?
    if (position.column === selectedArea.x0 &&
        position.row === selectedArea.y0) {
        classes = classes + ' ' + styleSheet.resizeMarker;
    }

    return classes;
}

export default cellRenderer;