import ReactDOM from 'react-dom';
import React from 'react';
import Spreadsheet from './../main/Spreadsheet';
import style from './style.scss';
import XLSX from 'xlsx';

class Demo extends React.Component {
    state = {
        columnsFormat: [
            { wpx: 202 }, , , ,
            { wpx: 384 }
        ],
        rowsFormat: [
            { hpx: 26.25 }, , ,
            { hpx: 27 },
        ],
        cellData: [
            ['January', 'February', , , 'Mars'],
            ['Kalmar', 'Ume', 'Stockholm', 'Gteborg'],
            ['1', '2', '3']
        ]
    }
    constructor(props) {
        super(props);

    }

    handleColumnResize = (payload) => {
        this.setState((prevState) => {
            let newColumnsFormat = [...prevState.columnsFormat];
            if (typeof prevState.columnsFormat[payload.column] === 'undefined') {
                // Extend array and insert element
                newColumnsFormat[payload.column] = 
                { 
                    wpx: payload.newSize
                }
            } else {
                newColumnsFormat[payload.column].wpx = payload.newSize;
            }
            return ({
                columnsFormat: newColumnsFormat
            });
        })
    }

    handleRowResize = (payload) => {
        this.setState((prevState) => {
            let newRowsFormat = [...prevState.rowsFormat];
            if (typeof prevState.rowsFormat[payload.row] === 'undefined') {
                // Extend array and insert element
                newRowsFormat[payload.row] = 
                { 
                    hpx: payload.newSize
                }
            } else {
                newRowsFormat[payload.row].hpx = payload.newSize;
            }
            return ({
                rowsFormat: newRowsFormat
            });
        })
    }

    render() {
        return (
            <div className={style.demo}>
                {/* <input type="file" onChange={handleFileSubmit} /> */}
                <h2>Spreadsheet Demo</h2>
                <div className={style.output}>
                    <Spreadsheet
                        columnsFormat={this.state.columnsFormat}
                        rowsFormat={this.state.rowsFormat}
                        cellData={this.state.cellData}
                        onColumnResize={this.handleColumnResize}
                        onRowResize={this.handleRowResize}
                    ></Spreadsheet>
                </div>
            </div>
        )
    }
}

ReactDOM.render(
    <Demo></Demo>,
    document.getElementById('root')
);


