import React from 'react';
import style from './ResizeHandle.scss';
import ReactDOM from 'react-dom';

class ResizeHandle extends React.PureComponent {
    state = {
        showResizeLine: false,
        isResizing: false,
        startResizePos: undefined,
    };
    constructor(props) {
        super(props);
        this.draggableRef = React.createRef();
    }

    handleResize = (e) => {
        e.preventDefault();
        let element = e.target;
        let position = e.clientX;
        // TODO - Rewrite with addEventListener............
        document.onmouseup = closeDragElement.bind(this);
        document.onmousemove = handleElementDrag.bind(this);
        this.setState({
            showResizeLine: true,
            isResizing: true,
            startResizePos: position,
        });

        function handleElementDrag(e) {
            const offset = position - e.clientX;
            position = e.clientX;
            element.style.left = (element.offsetLeft - offset) + "px";
            element.style.position = 'absolute';
        }

        // let _this = this;
        function closeDragElement(e) {
            element.style.position = 'unset';
            document.onmousemove = null;
            document.onmouseup = null;
            this.setState({
                showResizeLine: false,
                isResizing: false,
            });

            let offset = position - this.state.startResizePos;

            this.props.onResize(offset);
        }
    }

    render() {
        return (
            <div
                className={style.container}
            >
                <div 
                    ref={this.draggableRef}
                    className={style.resizeHandle}
                    onMouseDown={this.handleResize}
                    >
                    { this.state.showResizeLine ? <div className={style.resizeLine}></div> : null }
                </div>
            </div>
        );
    }
}

export default ResizeHandle;