import React from 'react';
import reset from './reset.scss';
import style from './Spreadsheet.scss';
import { ScrollSync } from 'react-virtualized'
import PropTypes from 'prop-types';
import Table from './Table/index';
import HeaderBar from './HeaderBar/index';
import SideBar from './SideBar/index';

const GROWTH_MARGIN = 600;              // Scroll margin in pixels from where the sheets size will be extended
const GROWTH_SCALE = 20;                // Number of rows/columns the sheet will grow with at a time

// Used to override the normal scrolling behavior with automatic scrolling when a selection is being made
const SCROLL_MARGIN = 40;               // Inner margin(px) of sheet from scrolling will be activated
const SCROLL_DISTANCE = 25;             // Size of scrolling step(px)
const SCROLL_INTERVAL = 50;             // Stepping interval in ms for when automatic scrolling is set

class Spreadsheet extends React.Component {
    static propTypes = {
        columnsFormat: PropTypes.arrayOf(PropTypes.object),
        rowsFormat: PropTypes.arrayOf(PropTypes.object),
        cellData: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
        onColumnResize: PropTypes.func
    }

    static defaultProps = {
        defaultColumnWidth: 70,
        defaultRowHeight: 20,
        minColumnWidth: 10,
        minRowHeight: 10
    }

    state = {
        numberOfColumns: 0,
        numberOfRows: 0,
        isSelecting: false,
        selection: {
            startSelection: {
                row: -1,
                column: -1,
            },
            endSelection: {
                row: -1,
                column: -1,
            },
        },
        forceScroll: {          
            horizontal: 0,
            vertical: 0,
            interval: SCROLL_INTERVAL,
        },
        shouldRecomputateGridSize: false,
    }

    constructor(props) {
        super(props);
        this.sheetRef = React.createRef();
        console.log(props);
    }

    componentWillReceiveProps = (nextProps) => {
        /**
         * TODO
         * Check if the component recieves new columnsFormat  or rowFormat, and if so,
         * a force update on the sheet would be necessary. The currently rendered/visible cells in
         * the grid components wont be aware of the new sizes.
         * Rewrite.
         */
        if (nextProps.columnsFormat !== this.props.columnsFormat ||
            nextProps.rowsFormat !== this.props.rowsFormat
        ) {
            this.setState({
                shouldRecomputateGridSize: true,
            }, () => {
                this.setState({
                    shouldRecomputateGridSize: false,
                });
            });
        }
    }

    componentWillUnmount = () => {
        document.removeEventListener('mouseup', this.onMouseUp);
    }

    componentDidMount = () => {
        document.addEventListener('mouseup', this.onMouseUp);
    }

    getColumnWidth = (args) => {
        if (typeof this.props.columnsFormat[args.index] === 'undefined') {
            return this.props.defaultColumnWidth;
        } else {
            return this.props.columnsFormat[args.index].wpx;
        }
    }

    getRowHeight = (args) => {
        if (typeof this.props.rowsFormat[args.index] === 'undefined') {
            return this.props.defaultRowHeight;
        } else {
            return this.props.rowsFormat[args.index].hpx;
        }
    }

    /**
     * Grow the sheet with the specified number of columns
     * @param {*} sizeToGrow 
     */
    growWidth(sizeToGrow) {
        this.setState((prevState) => ({
            numberOfColumns: prevState.numberOfColumns + sizeToGrow
        }));
    }

    /**
     * Grow the sheet with the specified number of rows
     * @param {*} sizeToGrow 
     */
    growHeight(sizeToGrow) {
        this.setState((prevState) => ({
            numberOfRows: prevState.numberOfRows + sizeToGrow
        }));
    }

    onScroll = (args) => {
        const distanceToRightEdge = args.scrollWidth - (args.scrollLeft + args.clientWidth);
        if (distanceToRightEdge < GROWTH_MARGIN) {
            this.growWidth(GROWTH_SCALE);
        }
        const distanceToBottom = args.scrollHeight - (args.scrollTop + args.clientHeight);
        if (distanceToBottom < GROWTH_MARGIN) {
            this.growHeight(GROWTH_SCALE);
        }
    }

    handleCellSelect = (cellPosition) => {
        this.setState({
            isSelecting: true,
            selection: {
                startSelection: {
                    row: cellPosition.rowIndex,
                    column: cellPosition.columnIndex,
                },
                endSelection: {
                    row: cellPosition.rowIndex,
                    column: cellPosition.columnIndex,
                }
            },
        });
        document.addEventListener('mousemove', this.handleMouseMoveScroll);
    }

    /**
     * When a selection is being made, checks if the cursor is at the edge of the
     * sheet, and if so, scrolls the sheet.
     */
    handleMouseMoveScroll = (e) => {
        const mouseX = e.clientX;
        const  mouseY = e.clientY;
        const rect = this.sheetRef.current.getBoundingClientRect();
        let forceScroll = {
            horizontal: 0,
            vertical: 0,
        }
        // Check horizontal scroll
        if (mouseX < rect.left + SCROLL_MARGIN) {
            forceScroll.horizontal = - SCROLL_DISTANCE;
        } else if (mouseX > rect.right - SCROLL_MARGIN){
            forceScroll.horizontal = SCROLL_DISTANCE;
        }
        // Check vertical scroll
        if (mouseY < rect.top + SCROLL_MARGIN) {
            forceScroll.vertical = - SCROLL_DISTANCE;
        } else if (mouseY > rect.bottom - SCROLL_MARGIN) {
            forceScroll.vertical = SCROLL_DISTANCE;
        }
        this.setState((prevState) => ({
            forceScroll: {
                ...prevState.forceScroll,
                ...forceScroll
            }
        }));
    }

    onMouseUp = () => {
        this.setState((prevState) => ({
            isSelecting: false,
            forceScroll: {
                ...prevState.forceScroll,
                horizontal: 0,
                vertical: 0,
            }
        }));
        document.removeEventListener('mousemove', this.handleMouseMoveScroll);
    }

    handleCellMouseOver = (cellPosition) => {
        if (this.state.isSelecting) {
            this.setState((prevState) => ({
                selection: {
                    startSelection: prevState.selection.startSelection,
                    endSelection: {
                        row: cellPosition.rowIndex,
                        column: cellPosition.columnIndex,
                    }
                }
            }));
        }
    }

    handleColumnSelect = (columnIndex) => {
        this.setState((prevState) => ({
            selection: {
                startSelection: {
                    row: 0,
                    column: columnIndex,
                },
                endSelection: {
                    row: 999999, // simply a high number, doesn't matter what it is
                    column: columnIndex,
                },
            }
        }));
    }

    handleRowSelect = (rowIndex) => {
        this.setState((prevState) => ({
            selection: {
                startSelection: {
                    row: rowIndex,
                    column: 0,
                },
                endSelection: {
                    row: rowIndex,
                    column: 999999, // simply a high number, doesn't matter what it is
                },
            }
        }));
    }

    handleColumnResize = (payload) => {
        const currentWidth = this.getColumnWidth({ index: payload.column});
        if (currentWidth + payload.offset > this.props.minColumnWidth) {
            this.props.onColumnResize
            ({
                newSize: currentWidth + payload.offset,
                column: payload.column
            });
        }
    }

    handleRowResize = (payload) => {
        const currentHeight = this.getRowHeight({ index: payload.row});
        if (currentHeight + payload.offset > this.props.minRowHeight) {
            this.props.onRowResize
            ({
                newSize: currentHeight + payload.offset,
                row: payload.row
            });
        }
    }

    render() {
        return (
            <div
                className={style.sheet}
                ref={this.sheetRef}
            >
                <GridRenderer
                    getColumnWidth={this.getColumnWidth}
                    getRowHeight={this.getRowHeight}
                    numberOfColumns={this.state.numberOfColumns}
                    numberOfRows={this.state.numberOfRows}
                    selection={this.state.selection}
                    onScrollMaster={this.onScroll}
                    onCellSelect={this.handleCellSelect}
                    onCellMouseOver={this.handleCellMouseOver}
                    onColumnSelect={this.handleColumnSelect}
                    onRowSelect={this.handleRowSelect}
                    forceScroll={this.state.forceScroll} // Override the normal scrolling behavior
                    onColumnResize={this.handleColumnResize}
                    onRowResize={this.handleRowResize}
                    shouldRecomputateGridSize={this.state.shouldRecomputateGridSize}
                ></GridRenderer>
            </div>
        )
    }
}

/**
 * Wraps & renders the HeaderBar, SideBar and Sheet and provides
 * synchronized scrolling functionality between them.
 * @param {*} props 
 */
function GridRenderer ({
    getColumnWidth,
    getRowHeight,
    numberOfColumns,
    numberOfRows,
    selection,
    onScrollMaster,
    onCellSelect,
    onCellMouseOver,
    onColumnSelect,
    onRowSelect,
    forceScroll,
    onColumnResize,
    onRowResize,
    shouldRecomputateGridSize,
}) {
    // let columnSelection, rowSelection, selectedArea;
    let selectedArea = calculateSelectedArea(
        selection.startSelection,
        selection.endSelection
    );
    let columnSelection = {
        from: selectedArea.x1,
        to: selectedArea.x0
    };
    let rowSelection = {
        from: selectedArea.y1,
        to: selectedArea.y0,
    }
    return (
        <ScrollSync>
            {({ clientHeight, clientWidth, onScroll, scrollHeight, scrollLeft, scrollTop, scrollWidth }) => (
                <div>
                    <div className={style.headerbarContainer}>
                        <HeaderBar
                            scrollLeft={scrollLeft}
                            rowHeight={25}
                            columnCount={numberOfColumns}
                            columnWidth={getColumnWidth}
                            selection={columnSelection}
                            onColumnSelect={onColumnSelect}
                            onColumnResize={onColumnResize}
                            shouldRecomputateGridSize={shouldRecomputateGridSize}
                        ></HeaderBar>
                    </div>
                    <div className={style.sidebarContainer}>
                        <SideBar
                            scrollTop={scrollTop}
                            columnWidth={40}
                            rowCount={numberOfRows}
                            rowHeight={getRowHeight}
                            selection={rowSelection}
                            onRowSelect={onRowSelect}
                            onRowResize={onRowResize}
                            shouldRecomputateGridSize={shouldRecomputateGridSize}
                        ></SideBar>
                    </div>
                    <div className={style.sheetContainer}>
                        <Table
                            onScroll={(args) => {onScroll(args); onScrollMaster(args);}}
                            columnCount={numberOfColumns}
                            rowCount={numberOfRows}
                            columnWidth={getColumnWidth}
                            rowHeight={getRowHeight}
                            onCellSelect={onCellSelect}
                            onCellMouseOver={onCellMouseOver}
                            selection={selection}
                            selectedArea={selectedArea}
                            forceScroll={forceScroll}
                            shouldRecomputateGridSize={shouldRecomputateGridSize}
                        ></Table>
                    </div>
                </div>
            )}
        </ScrollSync>
    )
}

/**
 * Calculate the selected area in coordinate form.
 * @param {*} startSelection 
 * @param {*} endSelection 
 */
function calculateSelectedArea(startSelection, endSelection) {
    let selectedArea = {};
    selectedArea.x1 = Math.min(startSelection.column, endSelection.column);
    selectedArea.y1 = Math.min(startSelection.row, endSelection.row);
    selectedArea.x0 = Math.max(startSelection.column, endSelection.column);
    selectedArea.y0 = Math.max(startSelection.row, endSelection.row);
    return selectedArea;
}

export default Spreadsheet;