import React from 'react';
import { Grid, AutoSizer } from 'react-virtualized';
import cellRenderer from './cellRenderer';
import style from './Table.scss';

class Table extends React.PureComponent {
    state = {
        automaticScrollIsSet: false,
        scrollLeft: 0,
        scrollTop: 0,
    }
    
    constructor(props) {
        super(props);
        this.gridRef = React.createRef();
    }

    componentWillReceiveProps(nextProps) {
        if (
            nextProps.forceScroll.vertical !== 0 ||
            nextProps.forceScroll.horizontal !== 0
        ) {
            if (this.state.automaticScrollIsSet === false) {
                this.setState({
                    automaticScrollIsSet: true,
                }, this.initiateAutomaticScroll());
            }
        }
    }

    componentDidUpdate() {
        if (this.props.shouldRecomputateGridSize) {
            this.gridRef.current.recomputeGridSize(); // Does a forceUpdate internally
        } else {
            // Grid will only do a shallow compare of props, which makes a forceUpdate necessary
            this.gridRef.current.forceUpdate();
        }
    }

    /**
     * Used whenever manual scroll is being overriden in favour
     * the values specified in props.forceScroll
     */
    initiateAutomaticScroll = () => {
        const intervalId = window.setInterval(() => {
            this.setState((prevState) => ({
                    scrollLeft: prevState.scrollLeft + this.props.forceScroll.horizontal,
                    scrollTop: prevState.scrollTop + this.props.forceScroll.vertical,
            }));
        }, this.props.forceScroll.interval);

        document.addEventListener('mouseup', clearInterval);

        let _this = this;
        function clearInterval() {
            window.clearInterval(intervalId);
            document.removeEventListener('mouseup', clearInterval);
            _this.setState({
                automaticScrollIsSet: false,
            });
        }
    }

    handleScroll = args => {
        // Keep track of the current scroll values for whenever initiateAutomaticScroll() takes control
        if (this.state.automaticScrollIsSet === false) {
            this.setState({
                scrollLeft: args.scrollLeft,
                scrollTop: args.scrollTop,
            });
        }
        this.props.onScroll(args);
    }

    render() {
        return (
            <AutoSizer>
                {({ height, width }) => (
                    <Grid
                        ref={this.gridRef}
                        height={height}
                        width={width}
                        cellRenderer={cellRenderer}
                        columnCount={this.props.columnCount}
                        columnWidth={this.props.columnWidth}
                        rowCount={this.props.rowCount}
                        rowHeight={this.props.rowHeight}
                        style={{ outline: 'none' }}

                        onScroll={this.handleScroll}
                        scrollLeft={this.state.scrollLeft}
                        scrollTop={this.state.scrollTop}
    
                        selection={this.props.selection}
                        selectedArea={this.props.selectedArea}
    
                        onCellMouseDown={this.props.onCellSelect}
                        onCellMouseOver={this.props.onCellMouseOver}

                        className={style.table}
                    />
                )}
            </AutoSizer>
        )
    }
}

export default Table;
