import React from 'react';
import styleSheet from './cellRenderer.scss';
import ResizeHandle from './ResizeHandle/ResizeHandle';

function cellRenderer({rowIndex, key, style, parent}) {
    let classes = styleSheet.cell;
    if (
        parent.props.selection &&
        rowIndex >= parent.props.selection.from &&
        rowIndex <= parent.props.selection.to
    ) {
        classes += ' ' + styleSheet.selected;
    }
    
    return (
        <div
            key={key}
            style={style}
            className={classes}
        >
            
            <Cell
                onClick={parent.props.onClick}
                rowIndex={rowIndex}
                onResize={parent.props.onCellResize}
            ></Cell>
        </div>
    )
}

class Cell extends React.PureComponent {
    constructor(props) {
        super(props);
    }

    handleClick = () =>{
        this.props.onClick(this.props.rowIndex);
    }

    handleResize = (offset) => {
        const payload = {
            offset: offset,
            row: this.props.rowIndex,
        }
        this.props.onResize(payload);
    }

    render() {
        return (
            <div
                onClick={this.handleClick}
            >
                { this.props.rowIndex }
                <ResizeHandle
                    onResize={this.handleResize}
                ></ResizeHandle>
            </div>
        )
    }
}

export default cellRenderer;